
.main{
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    flex-direction: column;
    

}

h2{
    margin-top: 5vh;
    border-bottom: 2px solid red;
    padding: 10px;
}

.flow{
    display: flex;
    justify-content: space-between;
    gap: 5vh;
    margin: 10vh;

}
.card{
    height: 70vh;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    
}
.imgs{
    height: 40vh;
    text-align: center;
}
img{
    height: inherit;    
}

.title{
text-align: center;
    font-size:larger;
    font-weight: bolder;
}

.desc{
    margin-top: 5vh;
    margin-left: 2vh;
    text-align: left;
}

.Link{    
    text-align: right;
    margin-right: 3vh;
}

