.contact_main{
    height: 50vh;
    width: 100vw;
    display: flex;
    align-items: center;
    flex-direction: column;

}
.contact_title{
    padding-top: 5vh;
    font-size: larger;
    font-weight: bolder;
    color: rgb(64, 8, 75);
    border-bottom: 4px solid red;
}

.contact_links{
    margin-top: 20vh;
    gap: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    
}
.contact_links a{
    color:blueviolet;
    padding: 20px;
    text-decoration: none;
    font-weight: bolder;

}
a:hover{
    color: rgb(32, 12, 51);
    font-weight: bolder;
    
}